.nearbySpotTemplate{
    margin:24px 0;
    height: calc(100% - 105px);
    overflow: auto;
    width: 100%;
}
.header{
    display: flex;
    justify-content: space-between;
    padding:0 20px 30px;
    
}
.image{
    width: 100%;
    height: 180px;
}
.image img{
    height: 100%;
    width: 100%;
}

.existingImage{
    object-fit: cover;
}
.inExistingImage{
    object-fit: contain;
}

.content{
    font-size: 15px;
    line-height: 20px;
    font-weight: normal;
    padding:24px;
}

.content .item{
    margin:12px 0;
    display: flex;
    flex-direction: row;


}
.content .text{
    font-size: 15px;
    line-height: 20px;
    margin-left: 5px;

}


.content .icon{
    width: 16px;

}
.content .icon svg{
    display: inline-block;
    width: 15px;
    height: 15px;
}

.description{
    border-bottom: 1px solid rgba(51,51,51,.2);
    border-top: 1px solid rgba(51,51,51,.2);
    padding:25px 0px;
    font-size: 15px;
    line-height: 20px;
    color:#4F4F4F;
}

@media screen and (min-width:600px) {
    .image{
        height:300px;
    }
}

@media screen and (min-width:900px) {
    .image{
        height:300px;
    }
    .image img{
        object-fit: contain;
    }
    .content{
        padding:24px 40px;
    }
    .description{
        padding:25px 30px;
    }
}