.modal{
    width: 100%;
    height: 100%;
    background-color: transparent;
    z-index: 1;
    position: fixed;
    top:0;
    left: 0;

    display: flex;
    justify-content: center;
    align-items: center;
}

.header{
    display: flex;
    justify-content: space-between;    
}

.header h4{
    font-size: 18px;
}

.content{
    background-color: white;
    width: 98%;
    height: 90%;
    box-shadow: 0px 4px 14px 4px #aaa;
    border-radius: 24px;
    max-width: 600px;
}
