.content{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(130px,1fr));
    grid-gap:10px;
    padding:25px 0px;
}

@media screen and (min-width:600px) {
    .content{
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(150px,1fr));
        grid-gap:15px;
        padding:25px 0px;
    }
}